import { Action, applyMiddleware, combineReducers, createStore, compose } from "redux";
import thunkMiddleware, { ThunkAction } from "redux-thunk";
import appReducer from "./app-reducer";
import authReducer from "./auth-reducer";
import learningTimeReducer from "./learningTime-reducer.ts";


let rootReducer = combineReducers({
	lerningTime: learningTimeReducer,
	app: appReducer,
	auth: authReducer

});

type RootReducerType = typeof rootReducer;
export type AppStateType = ReturnType<RootReducerType> //   TS




export  type InferActionTypes<T> = T extends {[key: string]: (...arg: any[])=> infer U } ? U : never//  reducer


export type BaseThunkType<A extends Action, R = Promise<void>> = ThunkAction<R, AppStateType, unknown, A>//  

declare global {
	interface Window {
		__REDUX_DEVTOOLS_EXTENSION_COMPOSE__?: typeof compose;
	}
}

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
let store = createStore(rootReducer, composeEnhancers(applyMiddleware(thunkMiddleware)));

export default store;
